@media (max-width:768px) {
    .img-block {
        flex-direction: column;
        width: 100%;
        padding-left: 0px;
        padding-right: 0px;

    }
    .content,
    .content1,
    .content2 {
        border-radius: 10px;
        filter: brightness(70%);
        height: 370px;
        width: 100%;
    }
    .text-block1,
    .text-block2,
    .text-block3 {
        margin-top: -50px;
        width: 100%;
        left: 0%;
    }
    .text-block1 p {
        color: #fffffff5;
        font-family: cursive;
        font-size: 20px;
        width: 99%;
        margin-top: 0;
        text-align: left;
        text-shadow: 2px 2px #000;
    }

  
}